.header {
  width: 100%;
  position:  fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}
.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}
.nav_logo,
.nav_toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}
.nav_list {
  display: flex;
  align-items: center;
  column-gap: 2rem;
}
.nav_link {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  &:hover::before {
    content: "";
    position: absolute;
    top:50%;
    left: 0;
    width: 100%;
    height: 1px; 
    background: #bf45ff;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
  }
  &:before {
    content: "";
    position: absolute;
    top:50%;
    left: 0;
    width: 100%;
    height: 1px; 
    background: #bf45ff;
    opacity: 0;
  }
}
.nav_icon,
.nav_close,
.nav_toggle {
  display: none;
}
/* Active link */
.active-link.nav_link:before {
  opacity: 1;
}

/*=============== BREAKPOINTS ===============*/
/* For medium devices */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }
  .nav {
    height: var(--header-height);
  }
  .nav_menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding:  2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0,0,0,0.15);
    border-radius:  1.5rem 1.5rem 0 0;
    transition: .3s;
  }
  .nav_link{
    &:before{
      top:57%;
    }
  }
  /* show menu */
  .show-menu {
    bottom: 0;
  }
  .nav_list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .nav_icon {
    font-size: 1.2rem;
  }
  .nav_close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }
  .nav_toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }
  .nav_icon,
  .nav_close,
  .nav_toggle {
    display: block;
  }
}
/* For small devices */
@media screen and (max-width: 350px) {
  .nav_menu {
    padding: 2rem 0.25rem 4rem;
  }
  .nav_list {
    column-gap: 0;
  }
}